<template>
    <div name=this.imgName>
        <img name="base-text" src="@/assets/images/logo.png" draggable="true" @dragstart="handleImgDragStart" />
        <img name="base-line" src="@/assets/images/logo.png" draggable="true" @dragstart="handleImgDragStart" />
        <img name="base-rect" src="@/assets/images/logo.png" draggable="true" @dragstart="handleImgDragStart" />
        <img name="base-ellipse" src="@/assets/images/logo.png" draggable="true" @dragstart="handleImgDragStart" />
    </div>
</template>

<script>
export default {
    data() {
        return {
            imgName: "",
        }
    },
    methods: {
        handleImgDragStart(e) {
            this.imgName = e.target.name;
        },
    },
};
</script>