<template>
    <div name=this.imgName>
        <img name="valve-rect" src="@/assets/images/valve-rect.png" draggable="true" @dragstart="handleImgDragStart" />
        <img name="valve-ellipse" src="@/assets/images/valve-ellipse.png" draggable="true"
            @dragstart="handleImgDragStart" />
        <img name="valve-gate" src="@/assets/images/valve-gate.png" draggable="true" @dragstart="handleImgDragStart" />
    </div>
</template>

<script>
import { defineComponent } from 'vue';
export default defineComponent({
    data() {
        return {
            imgName: "",
        }
    },
    methods: {
        handleImgDragStart(e) {
            this.imgName = e.target.name;
        },
    },
});
</script>