<template>
    <div name=this.imgName>
        <img name="pipe-line" src="@/assets/images/logo.png" draggable="true" @dragstart="handleImgDragStart" />
        <img name="pipe-rect" src="@/assets/images/logo.png" draggable="true" @dragstart="handleImgDragStart" />
    </div>
</template>

<script>
import { defineComponent } from 'vue';
export default defineComponent({
    data() {
        return {
            imgName: "",
        }
    },
    methods: {
        handleImgDragStart(e) {
            this.imgName = e.target.name;
        },
    },
});
</script>